import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, View, Group, Form, Card, Root,InfoRow,List,Cell,Link,PullToRefresh,Button} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		}


	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}


	getOwner(){
		var own="https://vk.com/id"+this.state.owner_id.toString();
		if(this.state.owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{this.state.owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}


	render() {

		const {back}=this.props;

		return (


				<Panel id="full">
					<PanelHeader
						left={<HeaderButton onClick={back} data-to="persik">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
						Events
					</PanelHeader>

					<Group onClick={back}><Div>{this.state.title}</Div></Group>
					<Group>
						<Div>
							<InfoRow title="Примечание">
								{this.state.text}
							</InfoRow>
						</Div>
					</Group>
					<Group>
						<List>
							<Cell>
								<InfoRow title="Место">
									{decodeURIComponent(this.state.mesto)}
								</InfoRow>
							</Cell>
							<Cell>
								<InfoRow title="Начало">
									{decodeURIComponent(this.state.starttime)}
								</InfoRow>
							</Cell>
							<Cell>
								<InfoRow title="Тип">
									{decodeURIComponent(this.state.type)}
								</InfoRow>
							</Cell>
							{this.getOwner()}
						</List>
					</Group></Panel>


		);
	}
}
const style={
test:{
	marginBottom:-4
}
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


