import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View,Root } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home';
import Persik from './panels/Persik';
import Full from './panels/Full';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: "home",
			fetchedUser: null,
            title:"",
            text:"",
            mesto:"",
            stattime:"",
            owner_id:"",
            activeStory: 'feed',
            chatlink:"",
            type:"",

		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })

	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} act={this.state.activePanel} />
				<Persik id="persik" go={this.go} act={this.state.activePanel}/>
				<Full id="full" go={this.go}/>

			</View>
		);
	}
}

export default App;
